:root {
  --primary-color: rgba(171, 75, 182, 1);
  --primary-color-2: rgba(195, 123, 203, 1);
  --secondary-color: rgba(51, 119, 182, 1);
  --secondary-color-2: rgba(44, 136, 222, 1);
  --button-border-color: rgba(61, 52, 145, 1);
  --button-gradient-normal: linear-gradient(to right, var(--primary-color) 0%, var(--secondary-color) 100%);
  --button-gradient-hover: linear-gradient(to right, var(--primary-color-2) 0%, var(--secondary-color-2) 100%);
  --dark: #0F0D13;
  --pink-light: #FDC5FD;
  --blue-light: #9BCCEB;
  --text-gradient: linear-gradient(100.84deg, var(--pink-light) -3.18%, var(--blue-light) 100%);
  --light: white;
}

h1, .h1, .section__title {
  font-size: 90px;
  line-height: 1;
}
@media (max-width: 1399px) {
  h1, .h1, .section__title {
    font-size: 75px;
  }
}
@media (max-width: 991px) {
  h1, .h1, .section__title {
    font-size: 40px;
  }
}
h2, .h2 {
  font-size: 50px;
  line-height: 1;
}
@media (max-width: 991px) {
  h2, .h2 {
    font-size: 40px;
  }
}
h3, .h3 {
  font-size: 40px;
  line-height: 1.25;
}
@media (max-width: 1399px) {
  h3, .h3 {
    font-size: 30px;
  }
}
@media (max-width: 991px) {
  h3, .h3 {
    font-size: 20px;
  }
}
h4, .h4 {
  font-size: 35px;
  line-height: 1;
  line-height: 1.25;
}

h5, .h5 {
  font-size: 25px;
  line-height: 1.25;
}
@media (max-width: 991px) {
  h5, .h5 {
    font-size: 20px;
  }
}

h6, .h6 {
  font-size: 20px;
}

p {
  font-size: 18px;
}
@media (max-width: 991px) {
  p {
    font-size: 16px;
  }
}
.noSelect {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.noSelect:focus {
  outline: none !important;
}

html {
  min-height: 100vh;
  background: var(--dark);
}

body {
  color: var(--primary-color);
  font-size: 16px;
  font-family: "Inter", sans-serif;
  font-weight: normal;
  font-style: normal;
  overflow-x: hidden;
  min-height: 100vh;
  line-height: 1.5;
}
body.overflow-hidden {
  overflow: hidden;
}

.container {
  max-width: 1790px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.d-none {
  display: none !important;
}

svg {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
}

main {
  position: relative;
}

section {
  display: block;
}

.section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
}
@media (max-width: 991px) {
  .section {
    min-height: auto;
  }
}
.section__header {
  display: flex;
  margin-bottom: 90px;
  position: relative;
}
@media (max-width: 991px) {
  .section__header {
    justify-content: flex-start !important;
  }
}
@media (max-width: 767px) {
  .section__header {
    flex-direction: column;
  }
}
.section__title {
  font-size: 36px;
  text-align: center;
  color: var(--light);
}
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: var(--dark);
  z-index: 1000;
  display: flex;
  opacity: 1;
  visibility: visible;
  display: none !important;
}
.preloader.hide {
  transition: all 0.5s;
  opacity: 0;
  visibility: hidden;
}
.preloader img, .preloader svg {
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  max-width: 100px;
  height: auto;
  display: flex;
  animation: 2s pulse infinite alternate;
}

@keyframes pulse {
  0% {
    opacity: 0.4;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0.6;
  }
}
