::-webkit-scrollbar {
  width: 16px;
}

::-webkit-scrollbar-track {
  background: #191620;
  transition: background 0.5s ease-in-out;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, rgb(160, 51, 160) 0%, rgb(35, 115, 165) 100%);
  border-radius: 10px;
  border: 3px solid #191620;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to bottom, rgb(160, 51, 160) 0%, rgb(35, 115, 165) 100%);
}

