.hero {
  min-height: 100vh;
}
.hero__categories-el-container {
  height: 160px !important;
}
.category-preview {
  margin-bottom: 56px;
}
.category-preview__title {
  font-size: 24px;
  color: var(--light);
  text-align: center;
  font-weight: 700;
}
.category-preview__title a {
  color: var(--light);
}
.category-preview__title a:hover {
  background: var(--text-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
}
.category-preview .row > * {
  margin-top: 40px;
  width: calc(100% / 4 - 30px + 30px / 4);
  margin-right: 30px;
}
.category-preview .row > *:nth-child(5n) {
  margin-right: 30px;
}
.category-preview .row > *:nth-child(4n) {
  margin-right: 0;
}
.category-preview .show-more {
  border: 1px solid var(--button-border-color);
  border-radius: 16px;
  height: 48px;
  width: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  background: linear-gradient(to right, rgba(171, 75, 182, 0.5) 0%, rgba(51, 119, 182, 0.5) 100%);
  transition: 0.5s all;
}
.category-preview .show-more svg {
  width: 8px;
  height: 14px;
}
.category-preview .show-more:hover {
  background: var(--button-gradient-normal);
}

.features__el {
  width: calc(100% / 4 - 30px + 30px / 4);
  margin-right: 30px;
}
.features__el:nth-child(5n) {
  margin-right: 30px;
}
.features__el:nth-child(4n) {
  margin-right: 0;
}
